export default {
    props: ['PageTopOptions'],
    computed: {
        PageTop() {
            var PageTop = this.PageTopOptions || {};
            var PageTopOptions = {
                title: PageTop.title || '标题',
                isShow: typeof PageTop.isShow === 'undefined' ? true : PageTop.isShow,
                vSearch: PageTop.vSearch || ''
            };
            return PageTopOptions;
        }
    },
    methods: {
        backout() {
            this.$emit('backout');
        }
    }
}

